import React,{ useEffect } from "react";
import { Sidebar, Menu, MenuItem } from 'react-pro-sidebar';
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from 'react-responsive-carousel';
import "./App.css";
import { LazyLoadImage } from "react-lazy-load-image-component";
import surah  from './surah.json';
import { instanceOf } from 'prop-types';
import {withCookies, useCookies,Cookies } from 'react-cookie';
import { Helmet } from 'react-helmet';

 class  Homepage extends React.Component {
  static propTypes = {
    cookies: instanceOf(Cookies).isRequired,
  };
   quranImages = () => {
    var imgs =[]
    for(var i=1;i<=624;i++){
      imgs.push(i)
    }
    return imgs 
  }
   goToPage = (event) => {
    const { cookies } = this.props;

    if(event.target.value<=604){
      cookies.set("quran", parseInt(event.target.value)+2,{ path: '/' })

       this.setState({
        selected: parseInt(event.target.value)+2,
    })
  }
    }
    goToSura = (event) => {
      const { cookies } = this.props;
      cookies.set("quran", parseInt(event.target.value)+2,{ path: '/' })

        this.setState({
          selected: parseInt(event.target.value)+2,
      })

      }
      goToJuz2 = (event) => {
        const { cookies } = this.props;

        if(event.target.value==1){
          this.setState({
            selected: 3,
        }) 
        cookies.set("quran", 3,{ path: '/' })
   
        }else{
          this.setState({
            selected: parseInt(event.target.value)*20+4-20,
        })
        cookies.set("quran", parseInt(event.target.value)*20+4-20,{ path: '/' })


        }

      }
  quranJuz2 = () => {
    var imgs =[]
    for(var i=1;i<=30;i++){
      imgs.push(i)
    }
    return imgs
  }
  quranSurah = () => {
    var suwar=[];
    for(var k in surah) {
      suwar.push(surah[k])      
   }
    return suwar
  }
   
    constructor(props) {
      const { cookies } = props;

        super(props);
        if(cookies.get("quran")=="undefined"){
          cookies.set("quran",1,{ path: '/' })
 
        }
        this.state = {
            loading: true,
            menuopen: true,
            imgs: this.quranImages(),
            juz2: this.quranJuz2(),
            allsura: this.quranSurah(),
            selected : cookies.get("quran")!="undefined"?cookies.get("quran"):1
          
          };
          this.quranImages()
          setTimeout(() => {
            this.setState({
                loading: false,
                menuopen: true,


            })
          }, 5000);
           }
           toggleIsActive = () => {
            this.setState({menuopen : !this.state.menuopen});
           }
    render() {

        return (
            <div>
               <Helmet>
          <title>{ "  -  " }</title>
        </Helmet>
            {this.state.loading  ? (
              <div className="app">
              <div className="app2">

                </div>  

                </div>
                  ) : (
                    <div className="app">
                    
                <img onClick={this.toggleIsActive} className="menu" src="./mushaf/logo.png"  />

                <Carousel className="quran"
                swipeable={true}
                showThumbs={false}
                showIndicators={false}
                showStatus={false}
                useKeyboardArrows={true}
                selectedItem={this.state.selected}
>
{this.state.imgs.map((data) => (
        <div>
              <LazyLoadImage
 className="quranimg" key={data} src={`./mushaf/standard2-quran_${data}.png`} />
                  </div>
      
      ))}

</Carousel>
<Sidebar className="sdmn" transitionDuration={2} collapsedWidth="0px" width="50%" backgroundColor="#123259" color="white" collapsed={this.state.menuopen} rtl={true}
                    >
                      <div className="side" >
                    <Menu
                    
                    >
                    <p>
                   Go to Page   
                   </p>
                    <input onChange={this.goToPage} type="number" min={1} max={624} className="inp" />

                    <p>
                   Go to Juz2   
                   </p>
                   <select onChange={this.goToJuz2}>
                   {this.state.juz2.map((data) => (
  <option className="opt" value={data}>{data}</option>
  ))}

</select>                    <p>
                   Go to surat    
                   </p>
                   <select onChange={this.goToSura}>
                    
                   {this.state.allsura.map((data)=>(
                  <option  className="opt" value={parseInt(data.pages)}>{data.title+"  "+data.titleAr}</option> 
                  ))}
</select> 
<p className="ftr">       </p>
      <p className="ftr">          </p>                
   </Menu>
                  </div>
                </Sidebar> 
               </div>                
                )               
      }

      </div>
        )
    }
}
export default withCookies(Homepage);

      
     